import ReactDOM from 'react-dom/client';
import React from 'react';
import './index.css';

// Simple test component
const TestApp = () => {
  return (
    <div style={{ padding: '20px', maxWidth: '600px', margin: '0 auto' }}>
      <h1>HELO Admin Dashboard</h1>
      <p>If you can see this message, the basic React rendering is working correctly.</p>
      <p>We are troubleshooting the main application loading issues.</p>
    </div>
  );
};

try {
  console.log('Mounting React application');
  ReactDOM.createRoot(document.getElementById('root')!).render(
    <React.StrictMode>
      <TestApp />
    </React.StrictMode>
  );
  console.log('React mounted successfully');
} catch (error) {
  console.error('Error mounting React:', error);
  document.body.innerHTML = '<div style="color:red; padding:20px;"><h1>Error Loading React</h1><p>' + 
    (error instanceof Error ? error.message : String(error)) + '</p></div>';
}